import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
import uView from 'uview-ui'
import Vue from 'vue'
import requset from './api/request';
import urls from "./api/urls";
import store from './store'
Vue.prototype.$ajax = requset;
Vue.prototype.$urls = urls;
uni.$on('gettoken',(tok)=>{
      return Vue.prototype.$urls = urls;
});
Vue.prototype.$store = store
Vue.config.productionTip = false
App.mpType = 'app'

try {
  function isPromise(obj) {
    return (
      !!obj &&
      (typeof obj === "object" || typeof obj === "function") &&
      typeof obj.then === "function"
    );
  }

  // 统一 vue2 API Promise 化返回格式与 vue3 保持一致
  uni.addInterceptor({
    returnValue(res) {
      if (!isPromise(res)) {
        return res;
      }
      return new Promise((resolve, reject) => {
        res.then((res) => {
          if (res[0]) {
            reject(res[0]);
          } else {
            resolve(res[1]);
          }
        });
      });
    },
  });
} catch (error) { }

// 载入uView库
Vue.use(uView)



const app = new Vue({
  ...App,
  store,
})
app.$mount()