<script>
	// 得到用户是否登录

	export default {
		onLaunch: function() {


		},
		onShow: function() {
			uni.getStorage({
				key: 'token', //数据key值，也就是你存储数据时的名称
				success: function(res) {
					console.log(res);
					//数据成功获取，用户已登录
					uni.setStorage({
						key: 'login',
						data: true,
					});
				},
				fail: function(res) {
					//数据未获取成功，用户没有登录，这里我们直接跳转到登录页面
					uni.setStorage({
						key: 'login',
						data: false,
					});
				},
			});
			const login =uni.getStorage('login');
			if(login&&this.globalData.tabbarIndex!==0){
				uni.setTabBarBadge({
				  index: 2,
				  text:this.globalData.tabbarIndex+'',
				  });
			};
			// 全局事件
			uni.$on('changeGo',(re)=>{
				if(re>0){
					this.globalData.tabbarIndex=re
					uni.setTabBarBadge({
					  index: 2,
					  text:re+'',
					  });
				}else if(re==0){
					uni.removeTabBarBadge({ //隐藏数字标
					  index:2 //tabbar下标
					})  
				}
			});


		},
		onHide: function() {

		},
		globalData: {
			tabbarIndex:0
		},

	}
</script>
<style lang="scss">
	/* 引入uView库样式 */
	@import "uview-ui/index.scss";
</style>
